.filter-created-container {
  display: flex;
  width: 100%;
  height: 45px;
  border: 1px solid;
  border-radius: 8px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  box-sizing: border-box;
  padding-left: 10px;
}

.filter-created-arrow {
  border: 0;
  padding-right: 25px;
  width: 20px;
  height: 20px;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  background: transparent;
}

.filter-created-arrow img {
  width: 15px;
  height: 15px;
  opacity: 0.2;
}

.filter-created-arrow:hover {
  cursor: pointer;
}

.filter-created-arrow img:hover {
  opacity: 0.4;
}

.input-block {
  display: flex;
  align-items: center;
  position: absolute;
  height: 70px;
  width: 265px;
  border: 1px solid;
  border-radius: 8px;
  z-index: 10;
  grid-template-columns: 1fr 1fr;
  margin-top: 10px;
}

.created-input-1 {
  width: 95px;
  height: 45px;
  position: absolute;
  border-radius: 8px;
  margin: 10%;
  border: 0;
  background: rgb(233, 233, 233);
  padding-left: 10px;
  box-sizing: border-box;
}

.created-input-2 {
  width: 95px;
  height: 45px;
  border-radius: 8px;
  right: -1px;
  position: absolute;
  margin: 10%;
  border: 0;
  background: rgb(233, 233, 233);
  padding-left: 10px;
  box-sizing: border-box;
}

#light .filter-created-container, .input-block {
  background-color: white;
  color: black;
  border-color: rgb(86, 86, 86);
}

#dark .filter-created-container, .input-block {
  background-color: black;
  color: white;
  border-color: white;
}

#light .input-block {
  background-color: white;
  color: black;
  border-color: rgb(86, 86, 86);
}

#dark .input-block {
  background-color: black;
  color: white;
  border-color: white;
}

#dark .filter-created-arrow img {
  filter: invert(100%);
  opacity: 0.7;
}

@media (min-width: 320px) {
  .input-block {
    height: 70px;
    width: 320px;
  }
  .created-input-1,
  .created-input-2 {
    width: 100px;
  }
}
@media (min-width: 768px) {
  .input-block {
    height: 70px;
    width: 160px;
  }
  .created-input-1,
  .created-input-2 {
    width: 60px;
  }
}
@media (min-width: 1024px) {
  .input-block {
    height: 70px;
    width: 220px;
  }
  .created-input-1,
  .created-input-2 {
    width: 80px;
  }
}
@media (min-width: 1366px) {
  .input-block {
    height: 70px;
    width: 265px;
  }
  .created-input-1,
  .created-input-2 {
    width: 95px;
  }
}

