.search {
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  padding: 10px;
  height: 45px;
  border: 1px solid;
  border-color: rgb(86, 86, 86);
  border-radius: 8px;
}

#light .search {
  background-color: white;
  color: black;
}

#dark .search {
  background-color: black;
  color: white;
  border-color: white;
}

