.Pagination {
    display: flex;
    padding-bottom: 5%;
    margin-top: 5%;
}

.PaginationPage {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    cursor: pointer;
    border: 1px solid black;
    border-right: none;
    color: black;
    background: inherit;

    &:first-of-type {
        border-radius: 8px 0 0 8px;
        cursor: pointer;
    }

    &:last-child {
        border-radius: 0 8px 8px 0;
        border-right: 1px solid;
        cursor: pointer;
    }

    &:only-child {
        border-radius: 8px;
    }

    &:hover {
        background: #ededed;
        color: black;
        border-color: black;
    }

    &:disabled {
        border-color: #b3b3b3;
        color: #b3b3b3;

        &:hover {
            cursor: auto;
            background: inherit;
        }
    }
}

.PaginationPage--dark {
    border: 1px solid white;
    color: white;
    border-right: none;

    &:hover {
        background-color: #464646;
        color: white;
        border-color: white;
    }

    &:disabled {
        border-color: #4c4c4c;
        color: #4c4c4c;
    }
}

.PaginationPage.PaginationPageWithActive {
    background: black;
    color: white;
    border-color: black;

    &:hover {
        cursor: auto;
        background: black;
        color: white;
        border-color: black;
    }
}

.PaginationPage.PaginationPageWithActive--dark {
    background: white;
    color: black;
    border-color: white;

    &:hover {
        background-color: white;
        color: black;
        border-color: white;
    }
}